
a {
  color: #000;
  text-decoration: none;
}
html {
  font-family: Helvetica, Tahoma, Arial, "PingFang SC", "Hiragino Sans GB", "Heiti SC", "Microsoft YaHei", sans-serif;
  font-size: 1vw;
}
body {
  font-size: 1.5vw;
  margin: 0;
}

@-webkit-keyframes fadeInDown {
from {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
}
to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
@keyframes fadeInDown {
from {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
}
to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
@-webkit-keyframes fadeInUp {
from {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
}
to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
@keyframes fadeInUp {
from {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
}
to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
.home {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100vw;
  height: 100vh;
}
.home .avatar {
  width: 10rem;
  height: 10rem;
  border-radius: 10rem;
  -webkit-animation: fadeInDown 0.9s ease-in-out;
          animation: fadeInDown 0.9s ease-in-out;
}
.home .login {
  margin: 4rem;
  -webkit-animation: fadeInUp 0.9s ease-in-out;
          animation: fadeInUp 0.9s ease-in-out;
}
.home .login:hover::before {
  padding-right: 2px;
  content: '{';
}
.home .login:hover::after {
  padding-left: 2px;
  content: '}';
}

.loading {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: #fff;
}
.loading .wait {
  -webkit-animation: blink 0.9s step-end infinite;
          animation: blink 0.9s step-end infinite;
}
@-webkit-keyframes blink {
50% {
    -webkit-box-shadow: 4px 0 #000;
            box-shadow: 4px 0 #000;
}
}
@keyframes blink {
50% {
    -webkit-box-shadow: 4px 0 #000;
            box-shadow: 4px 0 #000;
}
}



























































